:root {
  --bg: #fbf7f2;
  --surface: #fff;
  --ink: #2c2a28;
  --muted: #6e6761;
  --line: #e6ddd2;
  --caramel: #c98952;
  --cream: #f2e7da;
  --radius: 18px;
}

* {
  box-sizing: border-box;
}

body {
  color: var(--ink);
  background: var(--bg);
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 16px;
}

.header {
  border-bottom: 1px solid var(--line);
  background: var(--surface);
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  display: flex;
  position: sticky;
  top: 0;
}

.logo {
  color: var(--ink);
  font-family: Lora, serif;
  font-size: 22px;
  font-style: italic;
  text-decoration: none;
}

.nav {
  gap: 14px;
  display: none;
}

.nav a {
  color: var(--ink);
  font-size: 14px;
  text-decoration: none;
}

.contact {
  background: var(--cream);
  border: 1px solid var(--line);
  color: var(--muted);
  border-radius: 999px;
  padding: 8px 12px;
  font-size: 14px;
  text-decoration: none;
}

.filters {
  margin: 12px 0 18px;
}

.chip-row {
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 10px;
  display: flex;
}

.chip {
  border: 1px solid var(--line);
  background: var(--surface);
  cursor: pointer;
  border-radius: 999px;
  padding: 8px 12px;
  font-size: 14px;
}

.chip.is-active {
  background: var(--cream);
}

.grid {
  grid-template-columns: 1fr;
  gap: 12px;
  display: grid;
}

.card {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  padding: 14px;
}

.card__title {
  margin: 0 0 8px;
  font-size: 16px;
}

.card__meta {
  color: var(--muted);
  margin: 0 0 10px;
  font-size: 13px;
}

.priceRow {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

.badge {
  color: #fff;
  white-space: nowrap;
  background: #a9b6a0;
  border-radius: 999px;
  padding: 6px 10px;
  font-size: 12px;
}

.btn {
  background: var(--caramel);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 999px;
  width: 100%;
  padding: 10px 12px;
  font-weight: 600;
}

@media (width >= 720px) {
  .nav {
    display: flex;
  }

  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1280px) {
  .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.cartSummary {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  margin-top: 14px;
  padding: 14px;
}

.cartSummary__row {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.cartRow {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 10px;
  padding: 12px;
  display: flex;
}

.cartRow__title {
  font-weight: 600;
}

.cartRow__meta {
  color: var(--muted);
  margin-top: 6px;
  font-size: 13px;
}

.cartRow__right {
  align-items: center;
  gap: 10px;
  display: flex;
}

.qty {
  border: 1px solid var(--line);
  border-radius: 12px;
  width: 70px;
  padding: 8px;
}

.btnSecondary {
  border: 1px solid var(--line);
  background: var(--surface);
  cursor: pointer;
  border-radius: 999px;
  padding: 10px 12px;
}

.btnLink {
  text-align: center;
  background: var(--caramel);
  color: #fff;
  border-radius: 999px;
  width: 100%;
  margin-bottom: 8px;
  padding: 10px 12px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
}

.link {
  color: var(--muted);
  text-decoration: none;
  display: inline-block;
}

.field {
  margin: 10px 0;
  display: block;
}

.field span {
  color: var(--muted);
  margin-bottom: 6px;
  font-size: 13px;
  display: block;
}

.field input, .field select, .field textarea {
  border: 1px solid var(--line);
  border-radius: 12px;
  width: 100%;
  padding: 10px 12px;
}

.hidden {
  display: none;
}

body::-webkit-scrollbar {
  width: 8px;
}

.hero {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  margin-top: 12px;
  padding: 16px;
}

.muted {
  color: var(--muted);
}

.block {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  margin-top: 18px;
  padding: 14px;
}

.cardLink {
  background: var(--surface);
  border: 1px solid var(--line);
  border-radius: var(--radius);
  color: var(--ink);
  padding: 14px;
  font-weight: 600;
  text-decoration: none;
  display: block;
}

.cardLink:hover {
  background: var(--cream);
}

html {
  scroll-behavior: smooth;
}

#about, #reviews {
  scroll-margin-top: 110px;
}

.btn {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform .12s, filter .12s, box-shadow .12s;
}

.btn:hover {
  filter: brightness(.98);
}

.btn:active {
  filter: brightness(.94);
  transform: translateY(1px)scale(.99);
}

.btn:focus-visible {
  outline-offset: 2px;
  outline: 3px solid #c68c5c59;
}

.btn.is-added {
  filter: brightness(.92);
  box-shadow: inset 0 0 0 2px #ffffff59;
}

.cartLink {
  align-items: center;
  gap: 8px;
  display: inline-flex;
}

.cartBadge {
  text-align: center;
  color: #fff;
  background: #c68c5c;
  border-radius: 999px;
  min-width: 20px;
  height: 20px;
  padding: 0 6px;
  font-size: 12px;
  line-height: 20px;
  display: none;
}

.cartBadge.is-visible {
  display: inline-block;
}
